// 下拉选择页面
import {request} from '@/api/reques.js';
import {goWindow} from '@/api/navigate.js'  // 页面跳转
let _this = this;
export default{
	props:{
		status:{
			type:Number,
		}
	},
	data(){
		return{
			show1:false,
			show2:false,
			show3:false,
			show4:false,
			show5:false,
			show6:false,
			olds:[],
			higt:[],
			low:[],
			latest:[],
			num:'',
			car_type:[], // 车型
			sortItem:[
				{
					num:1,
					icon:"icon-chelingzuiduan",
					text:"车龄最短",
				},{
					num:2,
					icon:"icon--jiageyougaodaodi",
					text:"价格由高到低",
				},{
					num:3,
					icon:"icon--jiageyoudidaogao",
					text:"价格由低到高",
				}// ,{
				// 	num:4,
				// 	icon:"icon--zuixinshangxian",
				// 	text:"最新上线",
				// }
			],
			priceData:[{
				text:"20万以上",
				min_price:200000,
				max_price:9999999,
			},{
				text:"15-20万",
				max_price:210000,
				min_price:150000,
			},{
				text:"10-15万",
				max_price:160000,
				min_price:100000,
			},{
				text:"10万以下",
				max_price:110000,
				min_price:0,
			}],
			index1:0,
			array1:[], // 排放
			index2:0,
			array2:[], // 驱动
			index3:0,
			array3:["不限","0 - 199","200 - 399","400 - 600"],
			min_ml:0,
			max_ml:600,
			gatherItem:[],
			modelsCar:[],
			brandName:[],
			priceDrop:[],
			arrs:[],
			
		}
	},
	created() {
		_this = this;
		this.getSearchCondition()
	},
	watch:{
		"$store.state.cars":(v)=>{
			_this.handleBrands(v)
		},
		status(num){
			if(num == 1){
				_this.bindScreening()
				_this.handleReset()
			}else if(num == 2){
				_this.handleReset()
			}
			
		}
	},
	computed:{
		merge(){
			return this.arrs.concat(this.gatherItem,this.modelsCar,this.brandName,this.priceDrop)
		}
	},
	methods:{
		handlesort(){
			this.show1 = !this.show1
			this.show2 = false
			this.show3 = false
			this.show4 = false
			this.show5 = false
		},
		handlemodels(){
			this.show2 = !this.show2
			this.show1 = false
			this.show3 = false
			this.show4 = false
			this.show5 = false
		},
		handlebrand(){
			this.show3 = !this.show3
			this.show1 = false
			this.show2 = false
			this.show4 = false
			this.show5 = false
			goWindow("/pages/common/brand")
		},
		handleprice(){
			this.show4 = !this.show4
			this.show1 = false
			this.show2 = false
			this.show3 = false
			this.show5 = false
		},
		handlemore(){
			this.show5 = !this.show5
			this.show1 = false
			this.show2 = false
			this.show3 = false
			this.show4 = false
		},
		//车龄最短
		handleOld(e,text){
			this.gatherItem = [];
			this.num = e.currentTarget.dataset.num;
			this.gatherItem.push(text);
			this.show1 = false;
			this.$emit('handleOld',this.num)
			this.show6 = true;
		},
		// 获取车型,排放,驱动
		getSearchCondition(){
			request("/index/getSearchCondition",{},"GET")
			.then((res)=>{
				this.array2 = res.data.data.qd_type; // 驱动
				this.array1 = res.data.data.pf_type; // 排放
				this.car_type = res.data.data.car_type.slice(1); // 车型
				/* let arr = ["挂车"];
				this.car_type = carType.concat(arr) */
			})
		},
		// 根据车型筛选
		handleCarType(index,car){
			this.modelsCar = [];
			let carId = index + 1;
			this.$emit('handleCarType',carId);
			this.show2 = false;
			this.modelsCar.push(car);
			this.show6 = true;
		},
		// 根据品牌筛选
		handleBrands(brandObj){
			if(Object.keys(brandObj).length == 0)return
			this.brandName = [];
			this.$emit('handleBrands',brandObj);
			let brand = brandObj.brandName + brandObj.categoryName;
			this.brandName.push(brand);
			
			this.show6 = true;
			//this.$EventBus.$emit('handleBrands',brandData,brandId)
		},
		// 根据价格筛选
		handlePrice(index,max,min,text){
			this.priceDrop = [];
			this.$emit('handlePrice',index + 1,max,min);
			this.show4 = false;
			this.priceDrop.push(text);
			this.show6 = true;
		},
		// 排放筛选
		bindPickerChange1(e){
			this.index1 = e.target.value
		},
		// 驱动筛选
		bindPickerChange2(e){
			this.index2 = e.target.value	
		},
		// 马力筛选
		bindPickerChange3(e){
			this.index3 = parseInt(e.target.value)
			if(this.index3 == 0){
				this.tapMl(0,600);
			}else if(this.index3 == 1){
				this.tapMl(0,199);
			}else if(this.index3 == 2){
				this.tapMl(200,399);
			}else if(this.index3 == 3){
				this.tapMl(400,600);
			}
		},	
		// 马力
		tapMl(min,max){
			this.min_ml = min;
			this.max_ml = max;
			
		},
		// 确定按钮
		handleChange(){
			this.$emit('bindPickerChange1',true,this.index1,this.index2,this.min_ml,this.max_ml)
			this.show5 = false
		},
		// 重置按钮
		handleReset(){
			this.index1 = this.index2 = this.index3 = 0;
			this.$emit('bindPickerChange2',true)
			this.show5 = false
		},
		// 删除点击列表
		bindScreening(){
			this.gatherItem = this.modelsCar = this.brandName = this.priceDrop = [];
			this.$emit('bindScreening',true)
			this.show6 = false
		}
	}
}
